import React from 'react'
import {
  Card,
  CardHeader,
  CardBody,
  Typography
} from '@material-tailwind/react'

export default function SearchVoting() {
  return (
    <Card className='mt-24 w-full'>
        <CardHeader>
        <input 
        type="text"
        className='bg-purple-100 rounded-xl p-2 max-h-full w-full border-2 border-transparent focus:border-purple-300 focus:bg-purple-200 focus:border-2 focus:outline-none transition-colors'
         />
        </CardHeader>
      <CardBody className='text-center'>
        <Typography variant='h5' className='mb-2'>
          Votar
        </Typography>
        <Typography className='flex flex-col'>
          Introduce el ID de la votacin en la que desees participar
        </Typography>
      </CardBody>
    </Card>
  )
}
